import React from 'react';
import './itemMore.css';
import { PublicTitle } from '../../components/componentList/componentList';
import NestItem from '../nestItem/nestItem';
import ArticleItem from '../articleItem/articleItem';
import img from '../../view/submitNest/1.jpg';

const nestItem = {
  id: 2,
  img: img,
  name: '',
  userIcon: img,
  userName: '',
  lable: ['', '', '', ''],
  praise: 30,
};

const articleItem = {
  id: 1,
  img: [],
  name: '',
  info: '',
  praise: 30,
  userIcon: img,
  userName: '',
};

class Itemmore extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      nestItem: [nestItem, nestItem, nestItem],
      articleItem: [articleItem, articleItem, articleItem],
    }
  }

  componentDidMount() {

  }

  getNestItemDom() {
    let arr = [];
    this.state.nestItem.forEach((val, i) => {
      arr.push(<NestItem val={val} key={i} showUser history={this.props.history} />);
    });
    return arr;
  }

  getArticleItem() {
    let arr = [];
    this.state.articleItem.forEach((val, i) => {
      arr.push(<ArticleItem val={val} key={i} showUser history={this.props.history} />);
    });
    return arr;
  }

  render() {
    return (
      <div className="dt-itemMore">
        <div className="dt-itemMore-title"><PublicTitle val={'' + (this.props.type === 1 ? '' : '')} /></div>
        {
          this.props.type === 1
          ?
            this.getNestItemDom()
            :
            this.getArticleItem()
        }
      </div>
    );
  }
}

export default Itemmore; 


