import React from 'react';
import './evaluate.css';
import { PublicTitle } from '../../components/componentList/componentList';
import Utils from '../../common/Utils';

class Evaluate extends React.Component {

  constructor(props) {
    super(props);
    this.state = {

    }
  }

  componentDidMount() {

  }

  componentWillUnmount() {
    this.removeModal(this.evaluateModal);
    this.removeModal(this.replyModal);
  }

  removeModal(node) {
    node && node.parentNode && node.parentNode.removeChild(node);
  }

  handleGoList() {
    this.props.history.push('/itemEvaluate?itemId=');
  }

  getItemDom() {
    return (
      <div className="dte-item-box">
        <div className="dte-item borderBottom">
          <div className="l"><img src="./img/1.jpg" alt="" /></div>
          <div className="r">
            <div className="u">
              <p></p>
              <span>08-27</span>
            </div>
            <div className="i"><span onClick={() => this.handleReply()}>&nbsp;&nbsp;&nbsp;&nbsp;</span></div>

            <div className="reply"><span></span><span></span><span onClick={() => this.handleReply()}>&nbsp;&nbsp;&nbsp;&nbsp;</span></div>
            <div className="reply"><span></span><span></span><span onClick={() => this.handleReply()}>&nbsp;&nbsp;&nbsp;&nbsp;</span></div>

            <div className="load-more" onClick={() => this.handleGoList()}></div>
          </div>
        </div>
      </div>
    );
  }

  render() {
    return (
      <div className="dt-evaluate">
        <PublicTitle val={(this.props.type === 1 ? '' : '') + ''} />
        <div className="dte-input" onClick={() => this.handleEvaluate()}></div>
        {this.getItemDom()}
        <div className="load-more evaluate" onClick={() => this.handleGoList()}></div>
      </div>
    );
  }

  handleEvaluate() {
    this.evaluateModal = Utils.toast.modal({
      message: '<input class="evaluate-text-input" id="evaluateText" placeholder="" />',
      onOk: () => {
        let name = document.getElementById('evaluateText').value;
        if (name === '') {
          Utils.toast.info('');
          return;
        }

      },
    });
    setTimeout(() => {
      document.getElementById('evaluateText').focus();
    }, 300);
  }

  handleReply() {
    this.replyModal = Utils.toast.modal({
      message: '<input class="evaluate-text-input" id="replyText" placeholder="" />',
      onOk: () => {
        let name = document.getElementById('replyText').value;
        if (name === '') {
          Utils.toast.info('');
          return;
        }

      },
    });
    setTimeout(() => {
      document.getElementById('replyText').focus();
    }, 300);
  }

}

export default Evaluate; 


