import React from 'react';
import './info.css';

class Info extends React.Component {

  constructor(props) {
    super(props);
    this.state = {

    }
  }

  componentDidMount() {

  }

  handleGoUserHome() {
    this.props.history.push('/userHome?userId=');
  }
  
  render() {
    return (
      <div className="dt-info">
        <div className="dti-user">
          <div className="l" onClick={() => this.handleGoUserHome()}>
            <img src="./img/1.jpg" alt="" />
            <div className="i">
              <p></p>
              <span></span>
            </div>
          </div>
          <div className="r"></div>
        </div>
        <div className="dti-name"></div>
        <div className="dti-time">09-07&nbsp;&nbsp;20k</div>
        <div className="dti-lable"><em></em><em></em><em></em></div>
        <div className="dti-info">...</div>
        <div className="dti-num">
          <div><span className="iconfont icon--dianzan"></span><em>30</em></div>
          <div><span className="iconfont icon--shoucang"></span><em>30</em></div>
          <div><span className="iconfont icon-pinglun1"></span><em>&nbsp;30</em></div>
        </div>
      </div>
    );
  }
}

export default Info; 


